@import "_variables";
@import "_mixins";

@font-face {
	font-family: 'proxima-nova';
	src: url('../fonts/proxima-nova.eot');
	src: url('../fonts/proxima-nova.eot?#iefix') format('embedded-opentype'),
	url('../fonts/proxima-nova.woff2') format('woff2'),
	url('../fonts/proxima-nova.woff') format('woff'),
	url('../fonts/proxima-nova.svg#proxima_novaregular') format('svg') {}
	font-weight: normal;
	font-style: normal; }

@font-face {
	font-family: 'proxima-nova-bold';
	src: url('../fonts/proxima-nova-bold.eot');
	src: url('../fonts/proxima-nova-bold.eot?#iefix') format('embedded-opentype'),
	url('../fonts/proxima-nova-bold.woff2') format('woff2'),
	url('../fonts/proxima-nova-bold.woff') format('woff'),
	url('../fonts/proxima-nova-bold.svg#proxima-nova-bold') format('svg') {}
	font-weight: normal;
	font-style: normal; }

@font-face {
	font-family: 'proxima-nova-semibold';
	src: url('../fonts/proxima-nova-semibold.eot');
	src: url('../fonts/proxima-nova-semibold.eot?#iefix') format('embedded-opentype'),
	url('../fonts/proxima-nova-semibold.woff2') format('woff2'),
	url('../fonts/proxima-nova-semibold.woff') format('woff'),
	url('../fonts/proxima-nova-semibold.svg#proxima-nova-semibold') format('svg') {}
	font-weight: normal;
	font-style: normal; }


@font-face {
	font-family: 'proxima-nova-black';
	src: url('../fonts/proxima-nova-black.eot');
	src: url('../fonts/proxima-nova-black.eot?#iefix') format('embedded-opentype'),
	url('../fonts/proxima-nova-black.woff2') format('woff2'),
	url('../fonts/proxima-nova-black.woff') format('woff'),
	url('../fonts/proxima-nova-black.svg#proxima-nova-black') format('svg') {}
	font-weight: normal;
	font-style: normal; }

button,
input {
	font-family: 'proxima-nova';
	&:focus {
		outline: none; } }

.hidden {
	display: none !important; }

.mobile-hidden {
	@media (max-width: 767px) {
		display: none !important; } }

.mobile-visible {
	@include responsive(tabletdesktop) {
		display: none !important; } }

h1 {
	font-family: 'proxima-nova-black';
	margin: 0;
	font-size: 28px;

	@include responsive(tabletdesktop) {
		font-size: 48px; } }

h2 {
	font-family: 'proxima-nova-black';
	font-size: 20px;

	@include responsive(tablet) {
		font-size: 24px; }

	@include responsive(desktop) {
		font-size: 32px; }

	span {
		color: $pink; } }

strong {
	font-family: 'proxima-nova-semibold'; }

a {
	color: $pink; }

.bs-btn {
	border: 1px solid $pink;
	border-radius: 5px;
	box-sizing: border-box;
	padding: 11px;
	color: $text;
	display: flex;
	width: 100%;
	background-color: #fff;
	justify-content: center;
	cursor: pointer;
	font-family: 'proxima-nova-bold';
	transition: color .4s ease 0s, background .5s ease .2s, box-shadow .5s ease .2s;

	@include responsive(tabletdesktop) {
		font-size: 18px;
		padding: 16px; }

	&:hover:not([disabled]) {
		color: #ffffff;
		background: linear-gradient(98.83deg, $red 0.69%, $pink 96.99%);
		box-shadow: 0 5px 20px rgba(245, 90, 151, .5); }

	&:focus:not([disabled]) {
		outline: none; }

	&:disabled {
		border: 1px solid #bdbdbd;
		color: #bdbdbd;
		cursor: default; } }


body {
	margin: 0;
	background-color: $light-white;
	min-width: 375px;
	width: 100%;
	display: flex;
	flex-flow: column;
	justify-content: space-between;
	color: $text;
	font: 14px/1.3 'proxima-nova', sans-serif;

	@include responsive(tabletdesktop) {
		font: 16px/1.3 'proxima-nova', sans-serif; } }

header,
footer {
	min-height: 70px;
	background-color: $bg-dark; }

.header-content,
.footer-content {
	min-height: 70px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-flow: wrap; }

.footer-content {
	@include responsive(mobile) {
		padding: 20px 0; } }

.geo {
	color: #fff;
	margin-left: 32px;
	display: flex;
	align-items: center;

	@include responsive(mobile) {
		display: none; }

	span {
		margin-right: 12px; } }


.login {
	margin-left: auto;
	border-radius: 5px;
	background: linear-gradient(101.34deg, $red 0.69%, $pink 96.99%);
	padding: 2px;

	button {
		background-color: $bg-dark;
		border-radius: 5px;
		color: #fff;
		border: 0;
		padding: 10px 30px;
		cursor: pointer; } }

.user-login {
	margin-left: auto;
	display: flex; }

.favorite-link {
	background: url(../img/favorite-yes.svg) 0 0 no-repeat;
	padding-left: 30px;
	margin-right: 20px;

	@include responsive(desktop) {
		margin-right: 30px; }

	a {
		color: #fff;
		text-decoration: none; } }

.logout {
	background: url(../img/logout.svg) 0 0 no-repeat;
	padding-left: 24px;
	color: #fff;

	span {
		cursor: pointer; }

	a {
		text-decoration: none; } }

.copyright {
	text-align: center;
	color: rgba(255,255,255, .8);
	font-family: 'proxima-nova', sans-serif;

	@include responsive(mobile) {
		margin-top: 15px;
		text-align: center;
		width: 100%; }

	@include responsive(tabletdesktop) {
		order: 1; } }


.social {
	display: flex;

	@include responsive(tabletdesktop) {
		order: 2; }

	&__item {
		width: 30px;
		height: 30px;
		margin: 0 5px;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all .3s linear;

		&:hover {
			opacity: .9; }

		&--fb {
			background-color: #3B5998; }

		&--gp {
			background-color: #D73D32; }

		&--ok {
			background-color: #F2720C; }

		&--vk {
			background-color: #507299; } } }

main {
	margin-bottom: auto;
	overflow: hidden; }

.header {
	margin-top: 30px;
	position: relative;
	padding-left: 23px;

	@include responsive(tablet) {
		margin-top: 50px; }

	@include responsive(desktop) {
		margin-top: 70px; }

	&:before {
		content: '';
		position: absolute;
		width: 3px;
		top: 6px;
		bottom: 6px;
		left: 0;
		background: linear-gradient(90.08deg, $red 0.69%, $pink 96.99%); }

	h1 {
		line-height: 1; }

	&__sub {
		font-size: 18px;

		@include responsive(tabletdesktop) {
			margin-top: 8px;
			font-size: 20px; }

		span {
			color: $pink; } } }

.wrapper {
	width: 100%;
	max-width: 1170px;
	margin: 0 auto;
	padding: 0 20px;
	box-sizing: border-box; }

p {
	margin-top: 0; }


.main-search {
	max-width: 770px;
	position: relative;
	margin: 20px auto 20px;

	@include responsive(tabletdesktop) {
		margin: 50px auto 36px; }

	form {
		width: 100%; }

	.inp-search {
		background-color: #fff;
		border: 1px solid #E0E0E0;
		border-radius: 4px;
		width: 100%;
		display: block;
		color: #A4A4A4;
		font-family: 'proxima-nova';
		box-sizing: border-box;
		font-size: 14px;
		padding: 14px 70px 13px 20px;

		@include responsive(tabletdesktop) {
			padding: 12px 70px 12px 20px;
			font-size: 16px; } }

	.inp-search-submit {
		position: absolute;
		height: 40px;
		width: 50px;
		background: linear-gradient(92.55deg, $red 0.69%, $pink 96.99%);
		border-radius: 4px;
		right: 3px;
		top: 3px;
		border: 0;
		cursor: pointer;
		display: flex;
		justify-content: center;
		align-items: center;

		&:before {
			content: '';
			position: absolute;
			width: 20px;
			height: 20px;
			background: url(../img/search-icon.svg) 0 0 no-repeat; } } }


.sale-list {
	display: flex;
	flex-flow: wrap;
	//margin: 0 -15px
	padding: 0 0 50px;

	@include responsive(mobile) {
		margin: 0 -7px; }

	@include responsive(tablet) {
		margin: 0 -10px;
		padding: 0 0 50px; }

	@include responsive(desktop) {
		margin: 0 -15px;
		padding: 0 0 190px; }

	&__btn {
		position: relative;
		width: 100%;
		display: flex;
		justify-content: center;
		margin-top: 30px;

		@include responsive(tablet) {
			margin-top: 50px; }

		@include responsive($desktop) {
			margin-top: 70px; }

		&:before,
		&:after {
			content: '';
			position: absolute;
			z-index: -1;
			width: 100%;
			height: 1px;
			top: 50%;
			background: linear-gradient(175.95deg, $red 0.69%, $pink 96.99%); }

		&:before {
			left: -50%; }

		&:after {
			left: 50%; } } }

.show-more {
	max-width: 337px;
	border-radius: 5px;
	background: linear-gradient(101.34deg, $red 0.69%, $pink 96.99%);
	padding: 1px;
	position: relative;

	&:before,
	&:after {
		content: '';
		position: absolute;
		width: 20px;
		height: 100%;
		top: 0;
		background-color: $light-white; }

	&:before {
		left: -20px; }

	&:after {
		right: -20px; }

	button {
		background-color: #fff;
		border: 0;
		border-radius: 4px;
		cursor: pointer;
		font-size: 16px;
		font-family: 'proxima-nova-semibold';
		position: relative;
		padding: 15px 64px 15px 34px;

		@include responsive(tabletdesktop) {
			padding: 20px 64px 20px 34px; }

		&:after {
			content: '';
			position: absolute;
			width: 20px;
			height: 20px;
			margin-left: 10px;
			background: url(../img/plus-icon.svg) 0 0 no-repeat; } } }


.sale-item {
	background-color: #fff;
	border-radius: 15px;
	box-shadow: 0 5px 30px rgba(0,0,0, .05), inset 0 3px 0 $pink;
	//max-width: 370px
	text-align: center;
	padding: 30px;
	box-sizing: border-box;

	@include responsive(mobile) {
		width: calc(50% - 14px);
		padding: 20px 10px 15px;
		margin: 7px; }

	@include responsive(tablet) {
		width: calc(50% - 20px);
		padding: 20px;
		margin: 10px; }

	@include responsive(desktop) {
		width: calc(33.333334% - 30px);
		margin: 15px; }

	img {
		max-width: 100%; }

	&__text {
		margin: 20px 0; }

	&__size {
		background-color: #FFF9E9;
		border: 1px dashed $pink;
		border-radius: 4px;
		padding: 14px;
		box-sizing: border-box;

		@include responsive(mobile) {
			padding: 10px; }

		span {
			color: $pink;
			display: block;
			font-weight: 700; } }

	&__btn {
		margin: 20px 0; }

	&__favorite {
		color: $bg-dark;
		cursor: pointer;
		display: flex;
		justify-content: center;

		@include responsive(mobile) {
			margin-right: -30px; }

		&.active {
			.yes {
				display: block; }

			.no {
				display: none; } }

		.yes {
			display: none;
			position: relative;

			&:before {
				content: '';
				position: absolute;
				width: 20px;
				height: 20px;
				top: -1px;
				background: url(../img/favorite-yes.svg) 0 0 no-repeat;
				margin-left: -30px; }

			span {
				color: $pink; } }

		.no {
			position: relative;

			&:before {
				content: '';
				position: absolute;
				width: 20px;
				height: 20px;
				left: -30px;
				top: -1px;
				background: url(../img/favorite-no.svg) 0 0 no-repeat; } } } }

.hash-tags {
	flex-flow: wrap;
	display: flex;
	justify-content: center;

	@include responsive(tabletdesktop) {
		justify-content: flex-start; }

	@include responsive(desktop) {
		margin: 0 -4px 35px; }

	@include responsive(largedesktop) {
		border-bottom: 1px solid $border-color; }

	&__item {
		margin: 0 10px;
		padding: 0 4px 4px;
		cursor: pointer;

		@include responsive(mobile) {
			margin-bottom: 11px; }

		@include responsive(tabletdesktop) {
			margin-bottom: 16px; }

		@include responsive(largedesktop) {
			margin-bottom: 0; }

		&.active {
			font-family: 'proxima-nova-semibold';
			position: relative;

			&:after {
				content: '';
				position: absolute;
				left: 0;
				right: 0;
				height: 3px;
				bottom: -2px;
				background: linear-gradient(124.53deg, $red 0.69%, $pink 96.99%); } } } }


.box-modal {
	position: relative;
	width: 100%;
	box-sizing: border-box;
	background: #fff;
	border-radius: 20px;
	box-shadow: 0 50px 50px rgba(0,0,0, .2);
	padding: 15px;

	@include responsive(tablet) {
		padding: 20px; }

	@include responsive(desktop) {
		padding: 30px; }

	&--activate {
		@include responsive(desktop) {
			max-width: 770px; } }

	&--promo {
		@include responsive(desktop) {
			max-width: 970px; }

		.box-modal-close {
			right: 20px;
			top: 20px;
			background-color: #fff; } }

	&--login {
		box-shadow: 0 30px 30px rgba(0,0,0, .15), inset 0 3px 0 $pink;
		width: 335px;
		padding: 30px !important;

		@include responsive(tabletdesktop) {
			width: 380px; } } }

.box-modal-close {
	position: absolute;
	right: 15px;
	top: 10px;
	width: 30px;
	height: 30px;
	border-radius: 50%;
	border: 1px solid rgba(0,0,0, .12);
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	box-sizing: border-box;

	&:hover {
		color: #666; } }

.modal-header {
	display: flex;

	@include responsive(mobile) {
		flex-flow: column; }

	&__img {
		position: relative;
		overflow: hidden;
		border-radius: 10px;

		@include responsive(mobile) {
			margin-bottom: 20px;
			max-height: 360px; }

		@include responsive(tablet) {
			min-width: 267px;
			max-width: 267px; }

		@include responsive(desktop) {
			min-width: 360px;
			max-width: 360px;
			box-shadow: 0 5px 15px rgba(0,0,0, .2); }

		img {
			vertical-align: top;

			@include responsive(mobile) {
				max-width: 100%; }

			@include responsive(tabletdesktop) {
				position: absolute;
				height: 100%;
				width: auto; } } }

	&__sale-info {
		@include responsive(tablet) {
			width: calc(100% - 267px); }

		@include responsive(desktop) {
			width: calc(100% - 360px); }

		@include responsive(tabletdesktop) {
			padding-left: 30px; }

		h2 {
			margin: 0 0 20px;

			@include responsive(tabletdesktop) {
				margin: 0 40px 20px 0; } } } }


.promocode {
	max-width: 520px;
	margin-bottom: 10px;

	&__text {
		background-color: #F9F9F9;
		border: 1px solid #E0E0E0;
		border-radius: 4px;
		width: 100%;
		height: 50px;
		box-sizing: border-box;
		color: #A4A4A4;
		padding: 14px 5px 14px 20px;
		font-size: 16px;
		font-family: 'proxima-nova';
		position: relative;
		display: flex;
		justify-content: space-between;

		input {
			border: 0; }

		&.active {
			border-color: $pink;
			background-color: #fff;

			input {
				color: $pink; } } }


	&__buttons {
		position: absolute;
		right: 0;
		top: 0;
		width: 150px; }

	.promocode-btn {
		position: absolute;
		padding: 11px 14px 12px;
		background: linear-gradient(92.55deg, $red 0.69%, $pink 96.99%);
		border-radius: 4px;
		right: 5px;
		top: 4px;
		border: 0;
		cursor: pointer;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 14px;
		font-family: 'proxima-nova-semibold';
		color: #fff; } }

.promocode-info {
	display: flex;
	justify-content: space-between;
	max-width: 520px;
	font-size: 14px;

	&__code {
		display: flex;
		align-items: center;

		span {
			margin-left: 10px;
			cursor: pointer;
			display: flex; } }

	&__date {
		.date {
			color: $pink; } } }


.social-links {
	margin-top: 20px;


	&__item {
		display: flex;
		align-items: center;
		color: $text;
		margin-bottom: 12px;
		text-decoration: none; }

	img {
		margin-right: 10px; } }


.modal-tabs {
	margin-top: 20px;

	@include responsive(desktop) {
		margin-top: 30px; }

	&__links {
		display: flex;
		list-style: none;
		padding: 0;
		margin: 0;
		border-bottom: 1px solid $border-color;

		li {
			font-family: 'proxima-nova-bold';
			margin-right: 15px;
			padding: 0 12px;
			cursor: pointer;
			font-size: 14px;
			height: 26px;

			@include responsive(tabletdesktop) {
				height: 36px;
				font-size: 16px; }

			&.active {
				position: relative;
				color: $pink;

				&:after {
					content: '';
					position: absolute;
					left: 0;
					right: 0;
					height: 3px;
					bottom: -2px;
					background: linear-gradient(124.53deg, $red 0.69%, $pink 96.99%); } } } }

	&__content {
		> div {
			display: none;
			padding-top: 20px;

			&.active {
				display: flex;
				flex-flow: column; }

			p {
				&:last-child {
					margin-bottom: 0; } } } } }

.conditions {
	ul {
		padding: 0;
		margin-bottom: 0; }

	li {
		margin-bottom: 15px;
		list-style: none;
		position: relative;
		padding-left: 20px;

		&:last-child {
			margin-bottom: 0; }

		&:before {
			content: 'â€”';
			position: absolute;
			left: 0; } }

	&__min-summ {
		span {
			color: $pink; } } }

.places {
	display: flex;

	@include responsive(device) {
		flex-flow: column; }

	@include responsive(desktop) {
		flex-flow: wrap !important; }

	&__map {
		height: 350px;
		border-radius: 10px;
		overflow: hidden;

		@include responsive(desktop) {
			min-width: 500px; }

		iframe {
			box-sizing: border-box;
			border: 1px solid $border-color;
			border-radius: 10px;
			overflow: hidden; } }

	&__info {
		display: flex;
		flex-flow: column;
		justify-content: space-between;

		@include responsive(mobile) {
			margin-top: 15px; }

		@include responsive(tablet) {
			margin-top: 20px; }

		@include responsive(desktop) {
			padding-left: 30px;
			width: calc(100% - 530px); } }

	&__content {
		ul {
			margin: 0;
			padding: 0; }

		li {
			position: relative;
			list-style: none;
			padding-left: 18px;
			margin-bottom: 15px;

			@include responsive(mobile) {
				font-size: 14px; }

			&:before {
				content: '';
				left: 0;
				top: 4px;
				position: absolute;
				width: 10px;
				height: 10px;
				border: 1px solid $pink;
				border-radius: 10px; } } }

	&__social {
		display: flex;
		align-items: center;

		@include responsive(mobile) {
			flex-flow: column; }

		p {
			margin: 0 0 10px;

			@include responsive(tabletdesktop) {
				margin: 0 7px 0 0; } }

		.ya-share2__badge {
			border-radius: 50%; }

		.ya-share2__item {
			margin: 0 5px !important;

			&:before {
				content: ''; } }

		.ya-share2__icon {
			width: 30px !important;
			height: 30px !important;
			display: flex;
			align-items: center;
			justify-content: center;
			background-position: 50% 50% !important;
			background-repeat: no-repeat !important; }

		.ya-share2__item_service_facebook .ya-share2__icon {
			background-image: url(../img/fb-icon.svg); }

		.ya-share2__item_service_odnoklassniki .ya-share2__icon {
			background-image: url(../img/ok-icon.svg); }

		.ya-share2__item_service_vkontakte .ya-share2__icon {
			background-image: url(../img/vk-icon.svg); } } }

.activate {
	h2 {
		margin: 0 0 15px;

		@include responsive(tabletdesktop) {
			margin: 0 0 20px; }

		span {
			@include responsive(mobile) {
				display: block; } } }

	ol {
		padding: 0;
		list-style: none;
		counter-reset: li;
		margin: 0 0 20px;

		@include responsive(tabletdesktop) {
			margin: 0 0 30px; } }

	li {
		position: relative;
		padding-left: 45px;
		margin-top: 15px;
		min-height: 30px;
		display: flex;
		align-items: center;

		&:before {
			content: counters(li,"1");
			counter-increment: li;
			position: absolute;
			min-width: 30px;
			height: 30px;
			left: 0;
			background-color: $pink;
			border-radius: 50%;
			color: #fff;
			font-size: 15px;
			font-family: 'proxima-nova-bold';
			display: flex;
			align-items: center;
			justify-content: center; } }

	p {
		&:last-child {
			margin-bottom: 0; } } }

.modal-login {
	h3 {
		text-align: center;
		margin: 0;
		font-family: proxima-nova-bold;
		font-size: 24px;

		@include responsive(tabletdesktop) {
			font-size: 28px; } }

	&__form {
		margin-top: 20px;

		@include responsive(desktop) {
			margin-top: 30px; }

		form {
			margin: 0; }

		label {
			margin-bottom: 10px; } }

	&__step-1 {
		position: relative; }

	&__step-2 {
		position: absolute;
		display: none; }

	&.sms-result {
		.modal-login__step-1 {
			display: none;
			position: absolute; }

		.modal-login__step-2 {
			display: block;
			position: relative; } } }

fieldset {
	border: 0;
	display: flex;
	flex-flow: column;
	padding: 0;
	margin: 0 0 15px; }

.form-box {
	border: 1px solid $border-color;
	border-radius: 4px;
	display: flex;
	align-items: center;

	span {
		margin-right: 4px; }

	input {
		width: 100%;
		border: 0;
		font-family: proxima-nova;
		font-size: 16px; }

	&--phone {
		background: url(../img/phone-icon.svg) 16px 50% no-repeat;
		padding: 16px 16px 16px 38px; }

	&--sms {
		background: url(../img/sms-icon.svg) 16px 50% no-repeat;
		padding: 16px 16px 16px 45px; } }


.login-checkbox {
	position: relative;

	input {
		display: none;

		&:checked {
			+ label {
				&:before {
					background: url(../img/checkbox-icon.svg) 50% 50% no-repeat; } } } }

	label {
		margin-top: 24px;
		cursor: pointer;
		display: block;
		padding-left: 34px;

		@include responsive(tabletdesktop) {
			font-size: 14px; }

		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: -5px;
			width: 24px;
			height: 24px;
			border: 1px solid $border-color;
			border-radius: 3px; } } }

.code-number {
	font-size: 14px;
	margin: 15px 0 30px;

	span {
		color: $pink; } }

.code-resending {
	font-size: 14px;
	margin-bottom: 20px; }

.step-back {
	width: 24px;
	height: 24px;
	background: url(../img/back-arrow.svg) 0 0 no-repeat;
	position: absolute;
	top: -15px;
	left: -15px;
	cursor: pointer; }
